.app {
  text-align: center;
  padding: 20px;
  min-height: 100vh;
  background-size: cover;
  transition: background 0.5s, color 0.5s;
}

.app.light {
  background-image: url('https://images.unsplash.com/photo-1561484938-575f0b0c5272?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1350&q=80');
  color: #000;
}

.app.dark {
  background-image: url('https://images.unsplash.com/photo-1517867274874-15bc7c0a97f4?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1350&q=80');
  color: #fff;
}

.weather-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
}

.weather-card {
  border: 1px solid #ccc;
  border-radius: 10px;
  padding: 20px;
  width: 200px;
  background-color: rgba(255, 255, 255, 0.8);
  color: #000;
  transition: background 0.5s, color 0.5s;
}

.weather-card.dark {
  background-color: rgba(0, 0, 0, 0.8);
  color: #fff;
}

.weather-card h2 {
  margin: 0;
}

input {
  padding: 10px;
  margin-right: 10px;
}

button {
  padding: 10px;
}

button:hover {
  cursor: pointer;
}

.toggle-button {
  margin-bottom: 20px;
}

.error {
  color: red;
  margin-top: 10px;
}